@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600&display=swap);
.object-list-pane {
  overflow-y: auto;
}

.object-list-top {
  display: flex;
  flex-direction: column;
}

.object-list-title {
  margin: 2rem;
  margin-bottom: 1.5rem;
  margin-right: 1.5rem;
  margin-left: 1.5rem;
  font-weight: 600;
  color: var(--black-font-color);
}

.object-list-items {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.btn-light.object-list-items {
  position: relative;
  padding: 1rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  width: 100%;
  border: 0;
  height: 100%;
  z-index: 1;
  border-radius: 0;
  text-align: left;
  font-size: 1em;
  background-color: var(--window-background);
  color: var(--gray-font-color);
}

.btn-light.object-list-items:hover {
  z-index: 1;
  background-color: var(--hover-background);
  transition: all 0.1s ease-in-out;
}

.btn-check.object-list-items:active + .btn-light.object-list-items,
.btn-check.object-list-items:checked + .btn-light.object-list-items,
.btn-light.object-list-items.active,
.btn-light.object-list-items:active {
  z-index: 2;
  background-color: var(--hover-background);
}

.range-container {
  display: grid;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  width: 100%;
  text-align: center;
  place-items: center;
}

.field {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 1rem;
  width: 100%;
  top: 0.05rem;
}

.field input {
  height: 0.2rem;
  width: 100%;
  background: var(--lightgray-input);
  border-radius: 1rem;
  outline: none;
  border: none;
  -webkit-appearance: none;
}

.field input::-webkit-slider-thumb {
  border: none;
  border-radius: 50%;
  background: var(--window-background);
  height: 1rem;
  width: 1rem;
  cursor: pointer;
  box-shadow: 1px 1px 2.5px var(--box-shadow);
  -webkit-transition: box-shadow 0.5s ease;
  transition: box-shadow 0.5s ease;
  -webkit-appearance: none;
}

div .number-container {
  margin: 0;
  padding: 0;
  padding-top: 0.5rem;
  list-style: none;
}

.number-input-items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  padding-left: 0;
}

.number-input-wrapper,
.number-input-list {
  display: flex;
  padding: 0;
}

.number-input-icon {
  padding-right: 1.5rem;
  margin: 0.5rem;
  margin-right: 0;
  margin-left: 0;
}

.number-box {
  position: relative;
  margin-right: 0.5rem;
  height: 2.5rem;
  width: 5rem;
  border: 1px solid #d1d1d6;
  border-radius: 0.2rem;
}

.number-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: -webkit-min-content;
  width: min-content;
  height: calc(100% - 2px);
  top: 1px;
  right: 0.4rem;
  z-index: -9999;
  border-top-right-radius: 0.2rem;
  border-bottom-right-radius: 0.2rem;
  background-color: var(--window-background);
  color: var(--darkgray-input);
}

.number-icon.up {
  right: 0.3rem;
}

.number-buttons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  height: 100%;
  z-index: 9999;
}

.number-up,
.number-down {
  position: relative;
  cursor: pointer;
  border: none;
  width: 1.3rem;
  text-align: center;
  line-height: 1.5;
  padding: 0;
  border-top-right-radius: 0.2rem;
  border-bottom-right-radius: 0.2rem;
  border-left: 1px solid var(--lightgray-input);
  background-color: var(--window-background);
  color: var(--darkgray-input);
  transform: translateX(-100%);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.number-up:active,
.number-down:active {
  background: var(--lightgray-input);
}

.number-up {
  height: 50%;
  border-bottom: 1px solid var(--lightgray-input);
  border-radius: 0 4px 0 0;
}

.number-down {
  height: 50%;
  border-radius: 0 0 4px 0;
}

input[type='number'] {
  position: relative;
  width: 100%;
  height: 100%;
  line-height: 1.65;
  float: left;
  display: block;
  padding: 0;
  margin: 0;
  padding-left: 0.5rem;
  border: none;
  box-shadow: none;
  border-radius: 0.2rem;
}

input[type='number']:focus {
  outline: 0;
}

input[type='number']::-webkit-inner-spin-button, 
input[type='number']::-webkit-outer-spin-button {
  margin: 0.3rem;
  height: 2rem;
  border-radius: 1rem;
  z-index: 9999;
}

.axis {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  padding-right: 0.6rem;
  height: 100%;
  right: 0;
  z-index: 9998;
  border-top-right-radius: 0.2rem;
  border-bottom-right-radius: 0.2rem;
  background-color: var(--window-background);
  color: var(--darkgray-input);
}

.light-window-top {
  display: flex;
  flex-direction: column;
}

.light-window-title {
  margin: 2rem;
  margin-bottom: 1.5rem;
  margin-right: 1.5rem;
  margin-left: 1.5rem;
  font-weight: 600;
  color: var(--black-font-color);
}

.light-window-items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.light-window-items-intensity {
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.light-window-items-transformation {
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.light-window-items-transformation.label {
  display: block;
  padding: 0;
}

.light-window-items-header {
  display: flex;
  justify-content: space-between;
}

.toolbar {
  display: block;
  position: fixed;
  z-index: 1;
  margin: 2rem;
}

.btn-light.toolbar-items {
  padding: 0.7rem;
  padding-right: 0.8rem;
  padding-left: 0.8rem;
  border: 0;
  z-index: 1;
  background-color: var(--window-background);
  color: var(--black-font-color);
}

.btn-light.toolbar-items:hover:not(.active) {
  z-index: 1;
  background-color: var(--hover-background);
  color: var(--black-font-color);
  transition: all 0.3s ease-in-out;
}

.btn-check.toolbar-items:active + .btn-light.toolbar-items,
.btn-check.toolbar-items:checked + .btn-light.toolbar-items,
.btn-light.toolbar-items.active,
.btn-light.toolbar-items:active,
.show > .btn-light.toolbar-items.dropdown-toggle {
  z-index: 2;
  background-color: var(--brand-color);
  color: var(--white-font-color);
}

.btn-light:hover,
.btn-light:focus,
.btn-light:active,
.btn-light:active:focus:not(:disabled):not(.disabled),
.btn:focus,
.btn:active,
.btn:hover {
  box-shadow: none !important;
}

.model-window-top {
  display: flex;
  flex-direction: column;
}

.model-window-title {
  margin: 2rem;
  margin-bottom: 1.5rem;
  margin-right: 1.5rem;
  margin-left: 1.5rem;
  font-weight: 600;
  color: var(--black-font-color);
}

.model-window-items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  height: 3.5rem;
}

.model-window-items-transformation {
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.model-window-items-transformation.label {
  display: block;
  padding: 0;
}

.wrapper {
  position: relative;
  transform: scale(0.8);
}

.input {
  position: absolute;
  left: -9999px;
  top: -9999px;
}

.input[type='checkbox']:checked + span {
  background-color: var(--brand-color);
}

.input[type='checkbox']:checked + span:before {
  left: calc(100% - 3.5px);
  transform: translateX(-100%);
}

.wrapper > .slider {
  display: flex;
  position: relative;
  cursor: pointer;
  width: 47px;
  height: 25px;
  border-radius: 100px;
  background-color: var(--darkgray-input);
  transition: background-color 0.2s;
}

.wrapper > .slider:before {
  content: '';
  position: absolute;
  top: 3.5px;
  left: 3.5px;
  width: 18px;
  height: 18px;
  border-radius: 45px;
  transition: 0.2s;
  background: var(--window-background);
  box-shadow: 0px 1px 3px var(--box-shadow);
}

.wrapper > .slider:active:before {
  width: 21px;
}

.scene-window-top {
  display: flex;
  flex-direction: column;
}

.scene-window-title {
  margin: 2rem;
  margin-bottom: 1.5rem;
  margin-right: 1.5rem;
  margin-left: 1.5rem;
  font-weight: 600;
  color: var(--black-font-color);
}

.scene-window-items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  height: 3.5rem;
}

.scene-window-items:hover {
  background-color: var(--hover-background);
}

.btn-light.scene-window-items {
  font-size: 1em;
  background-color: var(--window-background);
}

.btn-light.scene-window-items:hover {
  background-color: var(--hover-background);
}

.camera {
  width: 3.5rem;
  border: 1px solid #d1d1d6;
}

.camera:focus {
  outline-color: #d1d1d6;
}

* {
  font-family: 'Inter', sans-serif;
  font-size: 0.9rem;
}

.navbar {
  z-index: 2;
  height: 3rem;
  border-bottom: 1px solid var(--navbar-border);
  background-color: var(--window-background);
}

.navbar-expand .navbar-nav .dropdown-menu {
  top: 3.3rem;
  border: 0;
  background-color: var(--window-background);
}

div .dropdown-item {
  color: var(--gray-font-color);
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

div .dropdown-item:hover {
  background-color: var(--hover-background);
}

div .dropdown-item.active,
.dropdown-item:active {
  background-color: transparent;
  color: var(--gray-font-color);
}

div .dropdown-item.active:hover,
.dropdown-item:active:hover {
  background-color: var(--hover-background);
  color: var(--gray-font-color);
}

div .dropdown-divider {
  border-top: 1px solid var(--navbar-divider);
  box-shadow: 0.4rem 0.4rem 0.5rem var(--box-shadow);
}

div hr {
  background-color: transparent;
}

.brand {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-style: italic;
  margin-top: 0.05rem;
  margin-left: 1.5rem;
}

.me-auto div {
  margin-left: 0.3rem;
  margin-right: 0.3rem;
}

#add-dropdown::after,
#file-dropdown::after,
#edit-dropdown::after {
  display: none;
}

/* Vertical Resizer */
.sc-htpNat.fXAXjb.sc-bdVaJa.cjjWdp {
  cursor: default;
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
  background-color: var(--resizer-vertical);
}

/* Horizontal Resizer */
.sc-bwzfXH.hEACtv.sc-bdVaJa.cjjWdp {
  height: 1px;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  background-color: var(--resizer-horizontal);
}

.pane-outliner {
  background-color: var(--window-background);
}

:root {
  --brand-color: hotpink;
  --canvas-background: #dddde0;
  --window-background: #fcfcfc;
  --hover-background: #f1f1f3;
  --white-font-color: #fcfcfc;
  --gray-font-color: #4f4f4f;
  --black-font-color: #000000e6;
  --navbar-border: #e4e4ea;
  --navbar-divider: #9b9ba2;
  --resizer-vertical: #e4e4ea;
  --resizer-horizontal: #b5b5c0;
  --lightgray-input: #ddd;
  --darkgray-input: #cccccc;
  --box-shadow: #787878;
}

* {
  box-sizing: border-box;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Inter', sans-serif;
  font-size: 0.9rem;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, avenir next, avenir,
    helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
}

html,
body,
#root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-size: 0.85em;
  background: #dddde0;
  background: var(--canvas-background);
  color: #4f4f4f;
  color: var(--gray-font-color);
}

